<script>
import PageContent from '@/components/page/content'
import TitleSecond from '@/components/page/title'
import RouterLink from '../../../components/list/mixins/router-link'
export default {
  name: 'myorder',
  components: { RouterLink, PageContent, TitleSecond },
  data () {
    return {
      checked: true,
      active: 0,
      ing: 'static/images/home/jz1.png',
      // 滚动加载相关数据
      scrollListData: {
        loading: false,
        finished: false
      }
    }
  },
  methods: {
    comment () {
      this.$router.push({path: '/evaluate/submit'})
    },
    refund () {
      this.$router.push({path: '/refund/application'})
    },
    todetail (id) {
      this.$router.push({path: '/order/detail', query: {id: id}})
    },
    onLoad () {
      // 异步更新数据
      setTimeout(() => {
        console.log('aaa')
        for (let i = 0; i < 8; i++) {
          this.recommendedList.push(1)
        }
        // 加载状态结束
        this.scrollListData.loading = false

        // 数据全部加载完成
        if (this.recommendedList.length >= 24) {
          this.scrollListData.finished = true
        }
      }, 500)
    },
    onSubmit () {
      this.$router.push({path: '/order/makesureorder'})
    }
  },
  created () {
  }
}

</script>

